import React, { useRef, useEffect, useState } from "react";
import { motion } from "framer-motion";
import {makeStyles} from '@material-ui/core'
import {Zoom,Fade} from 'react-reveal/';
import computer3 from './computer3.jpeg'
import {Typography} from '@material-ui/core'
import { useDencrypt } from "use-dencrypt-effect";

const useStyles = makeStyles(()=>({
    divScroll: {
        padding: '0',
        margin: '0',
        height: '100vh',
        backgroundImage: `url(${computer3})`,
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
        textAlign: 'center',
        color: 'white',
        // backgroundColor: 'blue',
        display: 'flex',
        flexDirection: 'column',
        justifyContent:'center',
        alignItems: 'center',
       
    },
    nombre: {
        fontWeight: '900',
        
    },
    profesion: {
        color: '#ffff00',
        height: '40px'

    }
}))

export default function Home() {
    const classes = useStyles()
    const values = ["Desarrollador Front End", "Desarrollador Front End",];
    const { result, dencrypt } = useDencrypt();
    
    useEffect(() => {
        let i = 0;
    
        const action = setInterval(() => {
          dencrypt(values[i]);
    
          i = i === values.length - 1 ? 0 : i + 1;
        }, 1000);
    
        return () => clearInterval(action);
    }, []);



  return(
      <div id="previous-content" className={classes.divScroll}>
        <Fade right >
          <Typography variant="h3" className={classes.nombre}>Federico Iván Leiva</Typography>
        </Fade>
        <Typography variant="h5" className={classes.profesion}>{result}</Typography>
      </div>
    
    
    
  );
}
