import React from 'react';
import {Container, makeStyles, Typography} from '@material-ui/core';
import html from './img/html.png'
import css from './img/css.png'
import js from './img/js.png'
import bootstrap from './img/bootstrap.png'
import react from './img/react.png'
import materialUi from './img/materialUi.png'

const useStyles = makeStyles((theme) => ({
    container: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        height: '100vh',
        // background: 'rgb(0,212,255)',
        // background: 'linear-gradient(180deg, rgba(0,212,255,1) 0%, rgba(61,170,140,1) 52%, rgba(70,227,109,1) 100%)',
    },
    containerTitulo: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        color: 'white',
        marginBottom: '20px',
        width: '300px',
        borderBottom: '2px solid white'
    },
    containerTecnologias: {
        display: 'flex',
        flexWrap: 'wrap',
        alignItems: 'center',
        justifyContent: 'center',
    },
    tecnologia: {
        width: '100px'
    },
    tecnologiaChica: {
        width: '130px'
    },
    tecnologiaImg: {
        width: '100%'
    },


}))

const tecnologias = {
    html: './img/html.png'
}

const Tecnologias = () => {
    const classes = useStyles();
    return ( 
        <div className={classes.container} id="tecnologias">
            <Container>
                <Container className={classes.containerTitulo}>
                    <Typography variant="h3">
                    Tecnologías
                    </Typography>
                </Container>
                <Container className={classes.containerTecnologias}>
                    <div className={classes.tecnologia}><img className={classes.tecnologiaImg} src={html} alt=""/></div>
                    <div className={classes.tecnologia}><img className={classes.tecnologiaImg} src={css} alt="" /></div>
                    <div className={classes.tecnologiaChica}><img className={classes.tecnologiaImg} src={bootstrap} alt=""/></div>
                    <div className={classes.tecnologia}><img className={classes.tecnologiaImg} src={js} alt=""/></div>
                    <div className={classes.tecnologia}><img className={classes.tecnologiaImg} src={react} alt=""/></div>
                    <div className={classes.tecnologiaChica}><img className={classes.tecnologiaImg} src={materialUi} alt=""/></div>
                </Container>
            </Container>
        </div>  
    );
}
 
export default Tecnologias;