import React, { useState } from 'react';
import {Container, makeStyles} from '@material-ui/core'
import { transform } from 'framer-motion';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import {Zoom,Fade} from 'react-reveal/';
import Drawer from './Drawer';
// import {Link} from 'react-router-dom'

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    navbar: {
        background: 'transparent',
        // height: '80px',
        display: 'flex',
        position: 'fixed',
        zIndex: '100',
        top: 0,
        width: '100%',
        color: 'white',
        textAlign: 'center',
        boxShadow: 'none',
        textDecoration: 'none',
        
    },
    navbarActive: {
        // height: '80px',
        display: 'flex',
        position: 'fixed',
        zIndex: '100',
        top: 0,
        width: '100%',
        textAlign: 'center',
        // background: 'linear-gradient(90deg, rgb(122, 2 ,194) 0%, rgb(60, 78, 194) 100%)',
        backgroundColor: 'white',
        transition: 'background .2s linear .2s',
        textDecoration: 'none',
    },
    title: {
        // flexGrow: 1,
        // backgroundColor: 'blue',
        width: '100%',
        textAlign: 'start',
        marginLeft: '40px'
    },
    containerButtons: {
        display: 'flex',
        height: '60px',
        // backgroundColor: 'blue',
        alignItems: 'center',
        justifyContent: 'space-evenly',
        width: '600px',
        right: '0',
        [theme.breakpoints.down('sm')] : {
            display: 'none',
        }
    },
    button: {
        padding: '0 20px 0 20px',
        // backgroundColor: 'blue',
        marginRight: '10px',
        "&:hover": {
            borderBottom: '2px solid white'
          }
    },
    toolbar: {
        // background: 'cyan',
        display: 'flex',
        justifyContent: 'space-between'
    },
    a: {
        textDecoration: 'none',
        color: 'white'
    },
    aActive: {
        textDecoration: 'none',
        color: 'black'
    }
}))

const Navbar = () => {
    const classes = useStyles();
    const [navbar, setNavbar] = useState(false);
    const [link, setLink] = useState(false);

    const changeBackground = () => {
        console.log(window.scrollY)
        if(window.scrollY >= 80){
            setNavbar(true);
        } else {
            setNavbar(false);
        }
    }
    const changeLink = () => {
        if(window.scrollY >= 80){
            setLink(true);
        } else {
            setLink(false);
        }
    }

    window.addEventListener('scroll', changeBackground)    
    window.addEventListener('scroll', changeLink) 

    return ( 
            <div className={classes.root}>
            <AppBar position="static" className={(navbar) ? classes.navbarActive : classes.navbar}>
                <Toolbar className={classes.toolbar}>
                    <Fade left>
                        <Typography variant="h5" className={classes.title}>
                            Portfolio
                        </Typography>
                    </Fade>
                    <div>
                        <Drawer />
                    </div>
                    <div className={classes.containerButtons}>
                        
                        <Fade top><a href="#divSobreMi" className={(link) ? classes.aActive : classes.a}><Button color="inherit" className={classes.button}>Sobre mi</Button></a></Fade>
                        <Fade top><a href="#tecnologias" className={(link) ? classes.aActive : classes.a}><Button color="inherit" className={classes.button}>Tecnologías</Button></a></Fade>
                        <Fade top><a href="#proyectos" className={(link) ? classes.aActive : classes.a}><Button color="inherit" className={classes.button}>Proyectos</Button></a></Fade>
                        <Fade top><a href="" className={(link) ? classes.aActive : classes.a}><Button color="inherit" className={classes.button}>Contáctame</Button></a></Fade>
                    </div>
                </Toolbar>
            </AppBar>
            </div>
    );
}
 
export default Navbar;