import React from 'react';
import {makeStyles, Typography, Button, Container, Avatar} from '@material-ui/core'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    Redirect,
    useLocation,
    useParams,
    NavLink
} from "react-router-dom";

import {Zoom,Fade, Bounce} from 'react-reveal/';

import { AnimatePresence, motion } from "framer-motion";

import comit from './comit.png'

const useStyles = makeStyles((theme) => ({
    container: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        height: '100vh',
        // background: 'rgb(79,79,79)',
        // background: 'linear-gradient(0deg, rgba(0,212,255,1) 0%, rgba(9,9,121,1) 80%, rgba(0,0,0,1) 100%)',
        color: 'white', 
    },
    containerBienvenido: {
        opacity: 100,
        transition: 'opacity 1s linear 1s'
    },
    containerPerfil: {
        // backgroundColor: 'white',
        borderRadius: '20px',
        width: '90%'
    },
    Link: {
        textDecoration: 'none',
        color: 'blue'
    },
    containerTitulo: {
        display: 'flex',
        marginBottom: '20px',
        // backgroundColor: 'blue',   
    },
    titulo: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-around',
        width: '350px',
        borderBottom: '2px solid white',

    }
}));

const pageVariants = {
    initial: {
      opacity: 0,
    },
    in: {
      opacity: 1,
    },
    out: {
      opacity: 0,
    },
  }


const Bienvenido = () => {
    const classes = useStyles();
    return(       
        <motion.div
        initial="initial"
        animate="in"
        exit="out"
        variants={pageVariants}
        >
            <div ></div>
            <Zoom>
                <Typography variant="h4" align="center">
                    ¡Bienvenido! 
                </Typography>
            </Zoom>
            <Bounce bottom>
                <Typography variant="h4" align="center">
                    Voy a contarte un poco mas sobre mí.
                </Typography>
            </Bounce>
            <Link to="/about"><Button>Siguiente</Button></Link>
        </motion.div>
    )
}

const text = 'Me llamo Fede, recibí las bases de la programación en la secundaria donde curse una Técnicatura en Programación. Luego de haber finalizado la cursada tomé la desición de seguir aprendiendo de esta linda profesión, así que me fui orientando en el Desarrollo Web del lado del Front End  ';

const Perfil = () => {
    const classes = useStyles();

    return (
        <motion.div
        initial="initial"
        animate="in"
        exit="out"
        variants={pageVariants}
        >
            <Container className={classes.containerPerfil}>
                {/* <div>
                    <img src={foto} alt=""/>
                </div> */}
                <Fade>
                    <Typography variant="h5">
                        Me llamo Fede, recibí las bases de la programación
                        en la secundaria donde curse una Tecnicatura en Programación.
                    </Typography>
                    <Typography variant="h5">
                        Luego de haber finalizado la cursada tomé la desición de seguir aprendiendo 
                        de esta linda profesión, así que me fui orientando en el Desarrollo Web
                        del lado del Front End
                    </Typography>
                    <Link className={classes.Link} to="/course"><Button variant="inline">Siguiente</Button></Link>
                </Fade>

            </Container>
        </motion.div>
    )
}

const Cursos = () => {
    const classes = useStyles()
    return(
        <motion.div
        initial="initial"
        animate="in"
        exit="out"
        variants={pageVariants}
        >
            <Container className={classes.containerTitulo}>
                <div className={classes.titulo}>
                    <Typography variant="h3">ComunidadIT </Typography>
                    <Avatar src={comit} />
                </div>
            </Container>
            <Container>
                <Typography variant="h4">A fines de Agosto de 2020, ComunidadIT me comunicó que había 
                sido preseleccionado para una de sus becas de estudio. </Typography>
                <Typography variant="h4">Allí comencé a aprender ReactJS.</Typography>
            </Container>
        </motion.div>
    )
}

const SobreMi = () => {
    const classes = useStyles();
    return ( 
       <div className={classes.container} id="divSobreMi">
           <Router>
                <AnimatePresence>
                    <Switch>
                        <Route exact path="/" component={Bienvenido} />
                        <Route exact path="/about" component={Perfil} />
                        <Route exact path="/course" component={Cursos} />
                    </Switch>
                </AnimatePresence>
            </Router>    
       </div>

    );
}
 
export default SobreMi;