import React from 'react';
import {makeStyles, Container, Typography, Card, CardActionArea, CardActions, CardMedia, Grid, Button, CardContent} from '@material-ui/core';
import GitHubIcon from '@material-ui/icons/GitHub';
import findYou from './findYou.png'

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 180,
    },
    container: {
        height: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        color: 'white'
        // background: 'rgb(70,227,109)',
        // background: 'linear-gradient(180deg, rgba(70,227,109,1) 0%, rgba(152,227,70,1) 52%, rgba(227,223,70,1) 100%)'
    },
    containerTitulo: {
        display: 'flex',
        justifyContent: 'center',
    },
    titulo: {
        display: 'flex',
        justifyContent: 'center',
        width: '250px',
        borderBottom: '2px solid white',
        
    },
    containerProyectos: {
        display: 'flex',
        justifyContent: 'center',
        marginTop: '20px'
    }
}))

const proyectos = [
    {
        nombre: 'Find You',
        descripcion: 'Find You es una Aplicación Web donde los usuarios van a poder reportar/ver y así mismo solicitar dar de baja a personas desaparecidas.',
        tecnologias: 'ReactJS, Hooks, API, Material UI y Firebase.',
        github: 'https://github.com/FredonRa/find-you-app',
        img: `${findYou}`
    },
]



const Tecnologias = () => {
    const classes = useStyles();

    const ListaProyectos = proyectos.length ? proyectos.map((proyecto) => {
        return(
            <Grid item xs={12} sm={6} md={4}>
            <Card className={classes.root}>
          <CardActionArea>
            <CardMedia
              className={classes.media}
              image={proyecto.img}
              title="Contemplative Reptile"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="h2">
                {proyecto.nombre}
              </Typography>
              <Typography variant="body2" color="textSecondary" component="p">
                {proyecto.descripcion}
              </Typography>
            </CardContent>
              <CardContent>
                <Typography variant="body2" color="textSecondary" component="p">
                    {proyecto.tecnologias}
                </Typography>
              </CardContent>
          </CardActionArea>
          <CardActions>
            <Button size="small" color="primary">
              Mas información
            </Button>
            <a href={proyecto.github} target="_blank">
            <Button size="small" color="primary">
              <GitHubIcon />
            </Button>
            </a>
          </CardActions>
        </Card>
        </Grid>
        )
    }): <></>

    return ( 
        <div className={classes.container} id="proyectos">
            <Container>
                <Container className={classes.containerTitulo}>
                    <div className={classes.titulo}>
                        <Typography variant="h3">
                            Proyectos
                        </Typography>
                    </div>
                </Container>
                <Grid container spacing={3} className={classes.containerProyectos}>
                    {ListaProyectos}
                </Grid>
            </Container>
        </div>  
    );
}
 
export default Tecnologias;